body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card{
  /* background-color: pink; */
  background-image: url(/Pratica-react-06-set-state/static/media/carrinho.5972eba8.webp);
  background-repeat: no-repeat;
  background-size: 100%;
  max-width: 500px;
  height: 330px;
  border-radius: 10px;
  border: 2px solid blueviolet;
  margin: 8rem auto;
  box-shadow: 10px 10px 15px gray;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.contador{
  height: 14rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-left: 10rem;
}

.quantidade{
  font-size: 1.5rem;
}

.btn_menos{
  background-color: rgb(113, 221, 236);
  width: 75px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 2rem;
  margin-right: 10px;
}

.btn_mais{
  background-color: rgb(202, 170, 244);
  width: 75px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 2rem;
}
